import React from "react";
import { Button, ConfigProvider, Dropdown, Flex } from "antd";
import { ReactComponent as DownArrow } from "../../assets/images/homepage/header/Chevron - Down (1).svg";
import { ReactComponent as Menu } from "../../assets/images/homepage/header/Menu (1).svg";
import { ReactComponent as Ellipse } from "../../assets/images/homepage/header/Ellipse 2.svg";
import { SmileOutlined } from "@ant-design/icons";

const Header = ({ setCollapsed }) => {
    const items = [
        {
            key: "1",
            label: "",
            icon: <SmileOutlined />,
        },
        {
            key: "2",
            label: "",
            icon: <SmileOutlined />,
            children: [
                {
                    key: "21",
                    label: "",
                },
                {
                    key: "22",
                    label: " ",
                },
            ],
        },
    ];

    const menuClickHandler = () => {
        setCollapsed((prevState) => !prevState);
    };

    return (
        <div className="header-container">
            <ConfigProvider direction={"ltr"}>
                <Flex align="center" justify="space-between">
                    <Button onClick={menuClickHandler} type="text">
                        <Menu />
                    </Button>

                    <Flex align="center" className="left-section">
                        <Dropdown
                            menu={{ items }}
                            arrow
                            placement="top"
                            direction={"ltr"}
                        >
                            <Button type="text" icon={<DownArrow />}>
                                 
                            </Button>
                        </Dropdown>

                        <Ellipse />
                    </Flex>
                </Flex>
            </ConfigProvider>
        </div>
    );
};

export default Header;
